@import url(partials/common/reset.css);
@import url(partials/common/jquery.fancybox.min.css);
/* Slider */
@import url(partials/common/jquery-ui.css);
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

* {
  outline: none; }

ul {
  list-style: none; }

body {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #393e3f;
  background-color: #fff; }

.container {
  position: relative;
  width: 98%;
  max-width: 1120px;
  margin: 0 auto; }

.btn:hover {
  opacity: 0.7; }

.header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 99;
  box-sizing: border-box;
  padding: 8px 0;
  border-bottom: 1px solid #e6e6e6;
  background-color: #fff; }
  .header .container {
    display: flex;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center; }
  .header-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 248px; }
    .header-logo-image {
      display: block;
      width: 100%; }
  .header-loginBtn {
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    min-height: 30px;
    width: 105px;
    font-weight: 500;
    text-decoration: none;
    color: #5c92e5;
    border: 1px solid #5c92e5;
    border-radius: 15px; }

.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end; }

.footer-logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 217px;
  margin-bottom: 16px; }
  .footer-logo-image {
    display: block;
    width: 100%; }

.footer-topRow {
  box-sizing: border-box;
  padding: 33px 0 48px;
  background-color: #4b65b8; }
  .footer-topRow-left {
    box-sizing: border-box;
    width: calc(100% - 262px);
    padding-right: 26px; }
  .footer-topRow-right {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    flex-direction: column;
    max-width: 262px; }

.footer-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  list-style: none; }
  .footer-menu-link {
    display: block;
    line-height: 28px;
    color: #fff;
    text-decoration: underline; }

.footer-infoLink {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 22px;
  font-weight: 500;
  line-height: 28px;
  text-decoration: none;
  color: #fff; }
  .footer-infoLink-img {
    display: block;
    width: 18px;
    margin-right: 9px; }
  .footer-infoLink + .footer-infoLink {
    margin-top: 7px;
    font-size: 15px; }
    .footer-infoLink + .footer-infoLink .footer-infoLink-img {
      width: 20px;
      margin-right: 7px; }

.footer-middleRow {
  padding: 34px 0 40px;
  background-color: #d6dcef; }
  .footer-middleRow .container {
    align-items: flex-start; }
  .footer-middleRow-left {
    box-sizing: border-box;
    width: calc(100% - 322px);
    padding-right: 64px; }
    .footer-middleRow-left p:not(:first-child) {
      display: block;
      margin-top: 18px;
      font-size: 14px;
      line-height: 22px; }
  .footer-middleRow-right {
    width: 322px; }

.footer-middleMenu-link {
  display: block;
  font-size: 14px;
  line-height: 32px;
  color: #393e3f; }

.footer-pays {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 20px; }
  .footer-pays-item {
    display: block;
    margin-right: 20px; }
    .footer-pays-item:last-child {
      margin-right: 0; }
  .footer-pays-link {
    display: block; }
    .footer-pays-link img {
      display: block; }

.footer-bottomRow {
  box-sizing: border-box;
  padding: 48px 0 72px;
  background-color: #c8cee0; }
  .footer-bottomRow .container {
    display: block; }
  .footer-bottomRow p {
    display: block;
    font-size: 14px;
    line-height: 24px;
    font-weight: 400; }
    .footer-bottomRow p a {
      font-weight: 500;
      color: #393e3f; }
  .footer-bottomRow p + p {
    margin-top: 34px; }
